import { useDispatch, useSelector } from 'react-redux'

import EncoderBinding from '../EncoderBinding'
import { selectLayers, updateSensorBindings } from './layersSlice'

function SensorBindings ({ layer, sensors }) {
  const dispatch = useDispatch()
  const layers = useSelector(selectLayers)
  const { parsed: sensorBindings = [] } = layers[layer].properties['sensor-bindings'] || {}

  function handleUpdate (sensorIndex, bindIndex, binding) {
    // TODO: handle sensor bindings list that doesn't match sensor count
    dispatch(updateSensorBindings({
      layerIndex: layer,
      sensorIndex,
      bindings: binding
    }))
  }

  return (
    <div style={{ margin: '0 0 30px' }}>
      <h3>Sensor bindings</h3>
      <div style={{
        display: 'flex',
        flexDirection: 'row',
        gap: '25px'
      }}>
        {sensors.map((sensor, i) => {
          const labelText = sensor.label || sensor.identifier
          const value = sensorBindings[i]?.value || '&inc_dec_kp'
          const params = sensorBindings[i]?.params || []
          return (
            <div key={i} style={{ display: 'block' }}>
              <label style={{ fontVariant: 'small-caps', fontSize: '90%' }}>
                <code>{labelText}</code>
              </label>
              <div style={{
                display: 'flex',
                flexDirection: 'row',
                gap: '5px',
                marginTop: '20px'
              }}>
                <EncoderBinding
                  direction="ccw"
                  context="encoders"
                  value={value}
                  params={params}
                  paramIndex={1}
                  onUpdate={bind => handleUpdate(i, 1, bind)}
                />
                <EncoderBinding
                  direction="cw"
                  context="encoders"
                  value={value}
                  params={params}
                  paramIndex={0}
                  onUpdate={bind => handleUpdate(i, 0, bind)}
                />
              </div>
            </div>
          )
        })}
      </div>
    </div>
  )
}

export default SensorBindings
